.link-blue-underlined {
  position: relative;
  padding: 9px 44px 9px 0;

  font-size: 15px;
  line-height: 22px;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  border-bottom: 2px solid rgba(57, 186, 240, 0.8);
  cursor: pointer;
  transition: opacity $transition-props;

  &::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 0;

    width: 28px;
    height: 14px;
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-size: contain;

    @include tablet {
      width: 23px;
      height: 11px;

      top: 12px;
    }
  }

  &:hover {
    opacity: 0.7;
  }

  @include tablet {
    padding: 9px 39px 9px 0;
  }
}
